	import Config from '../../config.js'
	import categorymask from '../common/common_mask/categorylistmask.vue'//
	import productmask from '../common/common_mask/productlistmask.vue'//
	import enterprisemask from '../common/common_mask/enterprisemask.vue'//
	import approvalmask from '../workflow/approving.vue'//
	import flowhistorymask from '../workflow/flowhistory.vue'//
	import flowmapmask from '../workflow/flowmap.vue'//
	import vewPoplemask from '../workflow/vewPople.vue'//
	import teststandardmask from '../common/common_mask/teststandardmask.vue'//
	import testprojectmask from '../common/common_mask/testprojectmask.vue'//
	export default {
		name: 'masks',
		components: {
			 approvalmask,
			 flowhistorymask,
			 flowmapmask,
			 vewPoplemask,
			 categorymask,
			 productmask,
			 enterprisemask,
			 teststandardmask,
			 testprojectmask
		},
		data() {
			 var validateItemleader = (rule, value, callback) => {//
                if (this.dataInfo.P_LEADERDesc === undefined || this.dataInfo.P_LEADERDesc === '' || this.dataInfo.P_LEADERDesc === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			 var validateproname = (rule, value, callback) => {//
                if (this.dataInfo.ITEM_NAME === undefined || this.dataInfo.ITEM_NAME === '' || this.dataInfo.ITEM_NAME === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			 var validateVname = (rule, value, callback) => {//
                if (this.dataInfo.V_NAME === undefined || this.dataInfo.V_NAME === '' || this.dataInfo.V_NAME === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
			};
			 //
            var price=(rule, value, callback) => { 
				var exp = /^(-)?\d{1,3}(,\d{3})*(.\d+)?$/;
				if(value != '' && value!=undefined){
					if(exp.test(value)==false){ 
	                    callback(new Error(''));
	              }else{
	                    callback();
	                }
				}else {
					callback();
				}
           };
			return {
				approvingData:{},//
				loadSign:true,//
				commentArr:{},
				falg:false,//
				basic_url: Config.dev_url,
				gridData: [], //
				fileList: [],
				resourceCheckedKey: [], // 
				getCheckboxData: {},
				resourceProps: {
					children: "children",
					label: "fullname"
				},
				page: {
					currentPage: 1,
					pageSize: 20,
					totalCount: 0
				},
				searchList: {
					nickname: '',
					username: '',
				},
				value: '',
				selUser: [],
				edit: true, //
				noedit: false,
				editSearch: '', //
				col_but1: true,
				col_but2: true,
				show: false,
				isok1: true,
				isok2: false,
				down: true,
				up: false,
				type: '',
				addtitle: true, //titile
				modifytitle: false, //titile
				viewtitle: false, //title
				modify: false,
				views: false,
				noviews: true, //
				approval:false,
				start:false,
				activeName: 'first', //tabs
				activeNames: ['1', '2', '3', '4', '5', '6', '7'], //
				labelPosition: 'right', //
				dialogVisible: false, //
				dataInfo: { //
					N_CODE: '',
					TYPE: '',
					XD_DATE: '',
					PRODUCT_TYPE:'',
					ITEM_NAME: '',
					ITEM_MODEL: '',
					V_NAME: '',
					VENDOR:'',
					CJDW: '',
					P_LEADER: '',
					TASKNUM: '',
					SOLUTION: '',
					COMPDATE: '',
					STATE: '1',
					STATEDesc: '',
					ENTERBY: '',
					STATUS: '',
					WORK_NOTICE_CHECKBASISList: [],
					WORK_NOTICE_CHECKPROJECTList: []
				},
				rules: {
					TYPE:[{required: true, message: '', trigger: 'change'}],
					TASKNUM:[{required: false, trigger: 'change', validator:this.Validators.isSpecificKey}],
					CJDW: [{required: true,trigger: 'change',message: '',}], //
					P_LEADERDesc: [{required: true, trigger: 'blur', message: ''}], //
					ITEM_NAME: [{required: true, trigger: 'blur', message:''}], //
					ITEM_MODEL: [
						{required: true,trigger: 'blur',message: ''},
						{trigger: 'blur', validator:this.Validators.isSpecificKey}
					], //
					V_NAME: [{required: true,validator: validateVname}], //
					VENDOR: [{required: true,trigger: 'blur',message: ''}], //
					QUALITY: [{required: true,message: ''},{ type: 'number', message: ''}], //
					CHECTCOST:[{required: false,trigger: 'change',validator:price}], //
					XD_DATE: [{type: 'string',required: true,message: '',trigger: 'change'}],//
					SOLUTION: [
						{required: true,trigger: 'blur',message: '',	},
						{trigger: 'blur',validator: this.Validators.isSpecificKey},
					],//
					MEMO: [
						{required: false, trigger: 'blur', validator: this.Validators.isSpecificKey},
					],
					// REMARKS: [{required: true,trigger: 'blur',message: '',}],
				},
				//tree
				resourceData: [], //
				selectData:[],//
				dataid:2,//id
				workNot:'workNot',//appname
				catenum:'',//
				pronum:'',//
				basisnum:'',////
				PRODUCT_TYPE:'',//
				username:'',
			};
		},
		methods: {
			//
			rowClass({ row, rowIndex}) {
			    return 'text-align:center'
			},
			dateFormat(row, column) {
				var date = row[column.property];
				if(date == undefined) {
					return "";
				}
				return this.$moment(date).format("YYYY-MM-DD");
			},
			reset() {
				this.dataInfo = {
					N_CODE: '',
					TYPE: '',
					XD_DATE: '',
					ITEM_NAME: '',
					PRODUCT_TYPE:'',
					ITEM_MODEL: '',
					V_NAME: '',
					VENDOR:'',
					CJDW: '',
					P_LEADER: '',
					TASKNUM: '',
					SOLUTION: '',
					COMPDATE: '',
					STATE: '1',
					STATEDesc: '',
					ENTERBY: '',
					STATUS: '',
					WORK_NOTICE_CHECKBASISList: [],
					WORK_NOTICE_CHECKPROJECTList: []
				}
				// this.$nextTick(() => {
				// 	this.$refs.form.resetFields();
				// });
				// this.$refs["dataInfo"].resetFields();
			},
			//
			getCompany() {
				var type = "2";
				var url = this.basic_url + '/api-user/depts/treeByType';
				this.$axios.get(url, {
					params: {
						type: type
					},
				}).then((res) => {
					this.selectData = res.data;
				});
			},
			addcategory(){//
				this.$refs.categorychild.visible(this.dataInfo.CJDW);
			},
			addproduct(){//
				this.$refs.productchild.visible(this.catenum);
			},
			//
			addCompany(type){
				this.$refs.enterprisechild.visible(type);
			},                                                                                                                                                                                                                                                                                                                                                                                                                                                                           

			handleNodeClick(data) { //
				//				console.log(data);
			},
			handleCheckChange(data, checked, indeterminate) {
				this.getCheckboxData = data;
			},
			getCheckedNodes() { //
				this.checkedNodes = this.$refs.tree.getCheckedNodes()
			},
			
			toNum(str) {
				return str.replace(/\,|\/g, "");
			},
			//
			toPrice() {
				var money = document.getElementById("cost").value;
				var num = parseFloat(this.toNum(money)).toFixed(2).toString().split(".");
				num[0] = num[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)', 'ig'), "$1,");
				this.dataInfo.CHECTCOST = num.join(".");
			},

			//tabs
			handleClick(tab, event) {
//				console.log(tab, event);
			},
			iconOperation(row, column, cell, event) {
				if(column.property === "iconOperation") {
					row.isEditing = !row.isEditing
				}
			},
			//
			deleteRow(index, rows) { //Table-
				rows.splice(index, 1);
			},
			sizeChange(val) {
				this.page.pageSize = val;
			},
			currentChange(val) {
				this.page.currentPage = val;
			},
			//
			addfieldBasis() {
				var obj = {
					NUMBER: '1',
					S_NUM: '',
					S_NAME: '',
					S_ENGNAME: '',
					VERSION: '',
					STATUS: '1',
					isEditing: true
				};
				this.dataInfo.WORK_NOTICE_CHECKBASISList.push(obj);
			},
			addfieldProject() {
				var obj = {
					NUMBER: '1',
					P_NUM: '',
					P_DESC: '',
					REMARKS: '',
					VERSION: '',
					STATUS: '1',
					isEditing: true
				};
				this.dataInfo.WORK_NOTICE_CHECKPROJECTList.push(obj);
			},

			//
			delfieldBasis(item) {
				var index = this.dataInfo.WORK_NOTICE_CHECKBASISList.indexOf(item);
				if(index !== -1) {
					this.dataInfo.WORK_NOTICE_CHECKBASISList.splice(index, 1);
				}
			},
			//
			visible() {
				this.reset();
				this.$axios.get(this.basic_url + '/api-user/users/currentMap', {}).then((res) => {
					this.dataInfo.DEPTID = res.data.deptId;
					this.dataInfo.ENTERBY = res.data.id;
					// this.dataInfo.ORGID = res.data.deptName
					var date = new Date();
					this.dataInfo.ENTERDATE = this.$moment(date).format("YYYY-MM-DD ");
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					})
				})
				
				this.addtitle = true;
				this.modifytitle = false;
				this.viewtitle = false;
				this.views = false; //
				this.noviews = true;
				this.edit = true;
				this.noedit = false;
			},
			detailgetData() {
				var url = this.basic_url +'/api-apps/app/workNot/' + this.dataid;
				this.$axios.get(url, {}).then((res) => {
					//
					for(var i = 0; i<res.data.WORK_NOTICE_CHECKBASISList.length;i++){
						res.data.WORK_NOTICE_CHECKBASISList[i].isEditing = false;
					}
					//
					for(var j = 0; j<res.data.WORK_NOTICE_CHECKPROJECTList.length;j++){
						res.data.WORK_NOTICE_CHECKPROJECTList[j].isEditing = false;
					}
					this.dataInfo = res.data; 
					this.dataInfo.CJDW=this.dataInfo.CJDWDesc;
					this.show = true;
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
			},	
			// 
			detail(dataid) {
				this.dataid=dataid;
				var usersUrl = this.basic_url + '/api-user/users/currentMap'
				this.$axios.get(usersUrl, {}).then((res) => {
					this.dataInfo.DEPTID = res.data.deptId;//id
					this.dataInfo.CHANGEBY = res.data.id;
					var date = new Date();
					this.dataInfo.CHANGEDATE = this.$moment(date).format("yyyy-MM-dd hh:mm:ss");
				}).catch((err) => {
					this.$message({
						message: '1',
						type: 'error'
					});
				});
				this.detailgetData();
				this.addtitle = false;
				this.modifytitle = true;
				this.viewtitle = false;
				this.noviews = true;
				this.views = false; //
				this.edit = true;
				this.noedit = false;
			},
			//
			view(dataid) {
				this.dataid=dataid;
				this.addtitle = false;
				this.modifytitle = false;
				this.viewtitle = true;
				this.views = true; //
				this.noviews = false;
				this.edit = true;
				this.noedit = true;
				this.detailgetData();
				this.isEditing=false;
				//
				var url = this.basic_url + '/api-apps/app/workNot/flow/isStart/'+dataid;
					this.$axios.get(url, {}).then((res) => {
					  if(res.data.resp_code==1){
							this.start=true;
							this.approval=false;
						}else{
						var url = this.basic_url + '/api-apps/app/workNot/flow/Executors/'+dataid;
						this.$axios.get(url, {}).then((res) => {
							var resullt=res.data.datas;
							var users='';
							for(var i=0;i<resullt.length;i++){
								users = users + resullt[i].username+",";
							}
							if(users.indexOf(this.username) != -1){
								this.approval=true;
								this.start=false;
							}
						});
						}
					});
			},
			// Begin
			handleRemove(file, fileList) {
//				console.log(file, fileList);
			},
			handlePreview(file) {
//				console.log(file);
			},
			handleExceed(files, fileList) {
				this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
			},
			beforeRemove(file, fileList) {
				return this.$confirm(` ${ file.name }`);
			},
			// End
			//
			close() {
				this.show = false;
				this.$emit('request');
			},
			open(){
				this.show = true;
			},
			toggle(e) {
				if(this.isok1 == true) {
					this.maxDialog();
				} else {
					this.rebackDialog();
				}
			},
			maxDialog(e) { //
				this.isok1 = false;
				this.isok2 = true;
				$(".mask_div").width(document.body.clientWidth);
				$(".mask_div").height(document.body.clientHeight - 60);
				$(".mask_div").css("top", "60px");
			},
			//
			rebackDialog() { //
				this.isok1 = true;
				this.isok2 = false;
				$(".mask_div").css("width", "80%");
				$(".mask_div").css("height", "80%");
				$(".mask_div").css("top", "100px");
			},
			//
			categorydata(value){
				this.catenum = value[0];
				this.dataInfo.PRODUCT_TYPE = value[1];
			},
			//
			appenddata(value){
				this.pronum = value[0];
				this.dataInfo.ITEM_NAME = value[1];
			},
			appendnames(value){
				this.dataInfo.V_NAME = value;//
			},
			
			// users/saveOrUpdate
			save() {
				this.$refs.dataInfo.validate((valid) => {
		          if (valid) {
							if(this.dataInfo.WORK_NOTICE_CHECKBASISList.length<=0&&this.dataInfo.WORK_NOTICE_CHECKPROJECTList.length<=0){
			        		this.$message({
								message: '',
								type: 'warning'
						    });
						return false;
			      }else{
			         var oDate1 = new Date(this.dataInfo.XD_DATE); //
    				 var oDate2 = new Date(this.dataInfo.COMPDATE);//
    				  if(oDate1.getTime() > oDate2.getTime()){ 
        						this.$message({
								message: '',
								type: 'warning'
							}); 
						return;	
    				  }
			        if(typeof(this.dataInfo.CJDW) != 'undefind') {
			        	 for(var j=0;j<this.selectData.length;j++){
			        	 	if(this.dataInfo.CJDW==this.selectData[j].fullname){
			        	 		this.dataInfo.CJDW=this.selectData[j].id
			        	 	}
			        	 }		
			        }
					var url = this.basic_url + '/api-apps/app/workNot/saveOrUpdate';
					this.$axios.post(url, this.dataInfo).then((res) => {
						if(res.data.resp_code == 0) {
							this.$message({
								message: '',
								type: 'success'
							});
							//
							this.$emit('request');
							this.reset();
						}
						this.falg=true
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
						this.falg=false
					});}
					this.falg=true
					} else {
						this.show=true;
					 	this.$message({
							message: '',
							type: 'warning',
						});
						this.falg=false
					}
				});
			},
			//
			saveAndUpdate() {
				this.save();
				if(this.falg){
					this.show = false;
				}
				
			},
			//
			saveAndSubmit() {
				this.save();
				this.show = true;
			},
			handleClose(done) {
				this.$confirm('')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			searchinfo() {
				this.page.currentPage = 1;
				this.page.pageSize = 20;
				this.addperbtn();
			},
			//
			addperbtn(type){
				if(this.dataInfo.CJDW==""||this.dataInfo.CJDW=="undefined"){
					this.$message({
						message: '',
						type: 'warning'
						});
				}else{
					var params = {
					page: this.page.currentPage,
					limit: this.page.pageSize,
					deptId: this.searchList.deptId,
					nickname: this.searchList.nickname,
					username: this.searchList.username,
				}
				var url = this.basic_url + '/api-user/users/usersByDept?deptId='+this.dataInfo.CJDW;
				this.$axios.get(url, {
					params: params
				}).then((res) => {
					this.gridData = res.data.data;
				});
				this.dialogVisible = true;	
				this.type=type;
				}
			},
			dailogconfirm() { //
				if(this.selUser.length == 0){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else if(this.selUser.length > 1){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					this.dialogVisible = false;
					if(this.type == 'leader') {
						this.dataInfo.P_LEADER = this.selUser[0].id;
						this.dataInfo.P_LEADERDesc = this.selUser[0].nickname;
					} else {
						this.dataInfo.ACCEPT_PERSON = this.selUser[0].id;
						this.dataInfo.ACCEPT_PERSONDesc = this.selUser[0].nickname;
					}
				}
			},
			//
			build(){
				var dataid = this.dataInfo.ID;
				    var url=this.basic_url + '/api-apps/app/workNot/operate/createInspectProxy?ID=' + dataid;
					this.$axios.get(url, {}).then((res) => {
						if(res.data.resp_code == 0) {
							this.$message({
								message: '',
								type: 'success'
							});
						}else{
							this.$message({
							message: '',
							type: 'warning'
							});
						}
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
			},
			SelChange(val) {
				this.selUser = val;
			},
			loadMore () {
			   if (this.loadSign) {
			     this.loadSign = false
			     this.page.currentPage++
			     if (this.page.currentPage > Math.ceil(this.page.totalCount/this.page.pageSize)) {
			       return
			     }
			     setTimeout(() => {
			       this.loadSign = true
			     }, 1000)
			     this.requestData()
			   }
			 },
			 //
			addbasis(value){
				this.basisnum = value[0];
				for(var i = 1;i<value.length;i++){
					this.dataInfo.WORK_NOTICE_CHECKBASISList.push(value[i]);
				}
			},
			 //
			addproject(value){
				for(var i = 0;i<value.length;i++){
					value[i].P_DESC = value[i].P_NAME;
					this.dataInfo.WORK_NOTICE_CHECKPROJECTList.push(value[i]);
				}
			},
			 //
			startup(){
				var url = this.basic_url + '/api-apps/app/workNot/flow/'+this.dataid;
				this.$axios.get(url, {}).then((res) => {
					if(res.data.resp_code == 1) {
							this.$message({
								message:res.data.resp_msg,
								type: 'warning'
							});
				    }else{
				    	this.$message({
								message:res.data.resp_msg,
								type: 'success'
							});
						var url = this.basic_url + '/api-apps/app/workNot/flow/Executors/'+this.dataid;
						this.$axios.get(url, {}).then((res) => {
								var resullt=res.data.datas;
								var users='';
								for(var i=0;i<resullt.length;i++){
								users = users + resullt[i].username+",";
							}
								if(users.indexOf(this.username) != -1){
									this.approval=true;
									this.start=false;
								}
								this.detailgetData();
						});
							
				    }
				});
			},
			//
			approvals(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workNot;
				var url = this.basic_url + '/api-apps/app/workNot/flow/isEnd/'+this.dataid;
		    		this.$axios.get(url, {}).then((res) => {
		    			if(res.data.resp_code == 0) {
							this.$message({
								message:res.data.resp_msg,
								type: 'warning'
							});
		    			}else{
		    				var url = this.basic_url + '/api-apps/app/workNot/flow/isExecute/'+this.dataid;
		    				this.$axios.get(url, {}).then((res) => {
				    			if(res.data.resp_code == 1) {
										this.$message({
											message:res.data.resp_msg,
											type: 'warning'
										});
								}else{
									this.$refs.approvalChild.visible();
								}
		    		});
		    		}
				});
			},
			//
			flowhistory(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workNot;
//				this.$refs.flowhistoryChild.open();
				this.$refs.flowhistoryChild.getdata(this.dataid);
			},
			//
			flowmap(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workNot;
				this.$refs.flowmapChild.getimage(this.dataid);
			},
			//
			viewpepole(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workNot;
				this.$refs.vewPopleChild.getvewPople(this.dataid);
			},
			//
			basisleadbtn(){
				this.$refs.standardchild.basislead(this.pronum);
			},
			//
			basisleadbtn2(){
				this.$refs.projectchild.projectlead(this.basisnum);
			},
		    getuser(){//
	            var url = this.basic_url + '/api-user/users/currentMap';
	            this.$axios.get(url, {}).then((res) => {//
	                    this.username = res.data.username;
	            }).catch((err) => {
	                this.$message({
	                    message: '',
	                    type: 'error'
	                });
	            });
        	},
		},
		mounted() {
			this.getCompany();
			this.getuser();
		},
	}
</script>

<style>
	@import '../../assets/css/mask-modules.css';
	.el-radio-group .el-col-4 {
		padding-top: 5px;
	}
	
	#cost {
		text-align: right !important;
	}
</style>


